@import "../../../style/theme/default/var.css";
@component-namespace transaction{
  @b content{
    padding:0 var(--theme-padding-large) var(--theme-padding-small);
    position: relative;
    overflow:hidden;
    @when cut{
    &:before{
      content: "";
      position: absolute;
      top:0;
      left:58.33333%;
      z-index: 1;
      display: block;
      height:100%;
      border-right:1px solid var(--theme-border-color-primary);
    }
    }

  }
  @b dialog {
    & .el-dialog__body {
      padding:0;
    }
  }
  @b title{
    position: relative;
    @when gao{
      & i {
        position: absolute;
        top: -12px;
        left: -var(--theme-padding-large);
        font-size: 30px;
        color: var(--theme-color-secondary);
      }
    }
  }
  @b detail {
    display: table;
    width:100%;
    background-color: var(--theme-background-color-primary);
    margin-top: 10px;
    & .p-left,
    & .p-right {
    display: table-cell;
    padding:var(--theme-padding-base) var(--theme-padding-base);
    vertical-align: top;
      }
    & .p-right{
    width:120px;
    text-align: right;
    }
  }
}



